<template>
  <div class="warps">
    <my-el-header />
    <div class="container">
      <div>
        <div class="form-nav">
          <div></div>
          <div class="scope">
            <el-button type="primary" @click="onSubmits"></el-button>
            <el-button type="primary" @click="black"></el-button>
          </div>
        </div>
      </div>
      <el-dialog destroy-on-close :close-on-click-modal="false" :title="listTitle"
                 :width="$store.state.dialogWidth" :visible.sync="listDialogFormVisible">
        <el-form :model="addListFrom" ref="formName" :rules="addRules">
          <div class="width">
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="rateName"
            >
              <el-input v-model="addListFrom.rateName" placeholder="" autocomplete="off"></el-input>
            </el-form-item>
          </div>

          <div class="width">
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="ratePrefix"

            >
              <el-input
                class="input"
                v-model="addListFrom.ratePrefix"
                autocomplete="off" placeholder=""
              ></el-input>
            </el-form-item>
          </div>
          <div class="width">
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="ratePrefix"
            >
              <el-select style="width: 100%;" v-model="addListFrom.area" placeholder="">
                <el-option
                  v-for="item in area"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </div>
          <div class="width">
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              required
              style="width: 100%"
            >
              <div class="flex">
                <el-form-item prop="rate">
                  <el-input v-model="addListFrom.rate" placeholder="()"></el-input>
                </el-form-item>
                <el-form-item label="" class="l-20" prop="billingPeriod">
                  <el-input v-model="addListFrom.billingPeriod" placeholder="()"></el-input>
                </el-form-item>
              </div>
            </el-form-item>
          </div>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <div class="footers">
            <p>: , 60(s)</p>
            <div class="footers">
              <el-button @click="listDialogFormVisible = false"> </el-button>
              <el-button type="primary" @click="submitForms('')"
              > 
              </el-button
              >
            </div>
          </div>
        </div>
      </el-dialog>
      <el-table height="800" :data="rateItemList" style="width: 100%; margin-top: 20px"
                v-if="rateItemList.length !== 0" :header-cell-style="{background:'#ccc', color: '#fff'}">
        <el-table-column align="center" prop="date" label="">
          <template scope="scope">{{ scope.$index + 1 }}</template>
        </el-table-column>
        <el-table-column align="center" prop="rateName" label="">
        </el-table-column>
        <el-table-column align="center" prop="rate" label="()">
        </el-table-column>
        <el-table-column align="center" prop="billingPeriod" label="">
        </el-table-column>
        <el-table-column align="center" prop="ratePrefix" label="">
        </el-table-column>
        <el-table-column align="center" prop="createTime" label="">
        </el-table-column>
        <el-table-column prop="area" align="center" label="" fixed="right">
          <template scope="scope">
            <div class="operate">
              <el-link type="info" @click="upDataForm(scope.row)"></el-link>
              <template>
                <el-popconfirm title="" @confirm="removeIt(scope.row)">
                  <el-link type="info" slot="reference">
                  </el-link>
                </el-popconfirm>
              </template>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <my-empty v-else />
    </div>
  </div>

</template>

<script>
import { addRateItemList, deleteRateItem, getRateItemList, upDaterateItem } from "@/newwork/ground-colltroner";
import myElHeader from "@/components/myElHeader";
import myEmpty from "@/newwork/myEmpty";
import { isValidNumber } from "@/util/validate";

export default {
  name: "rateList",
  data() {
    const validateNum = (rule, value, callback) => {
      if (!isValidNumber(value)) {
        callback(new Error("ip,"));
      } else {
        callback();
      }
    };
    return {
      formLabelWidth: "120px",
      listDialogFormVisible: false,
      listTitle: "",
      addListFrom: {
        rateName: "",  //
        billingPeriod: "60",//
        rateGroupId: "", //ID
        ratePrefix: "", //
        rate: "", //
        area: "home"
      },
      row: {},
      list: [],
      form: {},
      rateItemList: [],
      area: [
        { label: "", value: "abroad" },
        { label: "", value: "home" }
      ],
      addRules: {
        rateName: [
          { required: true, message: "", trigger: "blur" }
        ],
        ratePrefix: [
          { required: true, message: "", trigger: "blur" }
        ],
        rate: [
          { required: true, message: "", trigger: "blur" },
          { validator: validateNum, message: "", trigger: "blur" }
        ],
        billingPeriod: [
          { required: true, message: "", trigger: "blur" },
          { validator: validateNum, message: "", trigger: "blur" }

        ]
      }
    };
  },
  components: {
    myEmpty,
    myElHeader
  },
  methods: {
    onSubmits() {
      this.listDialogFormVisible = true;
      this.listTitle = "";
    },
    black() {
      this.$router.go(-1);
    },
    //
    submitForms() {
      this.$refs.formName.validate((valid) => {
        if (valid) {
          if (this.listTitle === "") {
            addRateItemList(this.addListFrom).then(res => {
              this.resetForm();
              if (res.data.code === 200) {
                this.$message.success("");
                this.listDialogFormVisible = false;
                this.getRateItemList(this.form);
              } else {
                this.$message.error(res.data.msg);
              }
            }).catch(e => {
              this.$message.error(e);
            });
          } else {
            upDaterateItem(this.addListFrom).then(res => {
              console.log(res);
              if (res.data.code === 200) {
                this.getRateItemList(this.form);
                this.listDialogFormVisible = false;
                this.$message.success("");

              } else {
                this.$message.error(res.data.msg);
              }
            });
          }
        } else {
          return false;
        }
      });
    },
    getRateItemList(form) {
      getRateItemList(form).then(res => {
        console.log(res);
        if (res.data.code === 200) {
          console.log(res);
          this.rateItemList = res.data.data.records;
        } else {
          console.log(res);
        }
      }).catch(e => {
        this.$message.error(e);
      });
    },

    //

    upDataForm(row) {
      this.addListFrom = row;
      this.listTitle = "";
      this.listDialogFormVisible = true;
    },
    //
    removeIt(row) {
      deleteRateItem(row.id).then(res => {
        if (res.data.code === 200) {
          this.$message.success("");
          //formid ok
          this.getRateItemList(this.form);
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },
    resetForm(type) {
      type === "form" ? this.$refs.form.resetFields() : this.$refs.formName.resetFields();
    }

  },
  created() {
    console.log(this.$route.query.rateGroupId);
    this.addListFrom.rateGroupId = this.$route.query.rateGroupId;
    this.form.rateGroupId = this.$route.query.rateGroupId;
    console.log(this.$route.query);
    this.getRateItemList(this.form);
  }
};
</script>

<style scoped>

.scope {
  text-align: right;
}

.flex {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.flex > * {
  flex: 1;
}

.footers {
  display: flex;
  justify-content: space-between;
}

.l-20 {
  margin-left: 20px !important;
}
</style>
